import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home/home'
import Archive from '../views/Archive/archive'
import Message from '../views/Message/message'
import About from '../views/About/about'
import Link from '../views/Link/link'
import Category from '../views/Category/category'
import Tag from '../views/Tag/tag'
import Article from '../views/Article/article'
import User from '../views/User/user'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: '李建阳的个人博客'
    }
  },
  {
    path: '/archives',
    name: 'Archive',
    component: Archive,
    meta: {
      title: '归档'
    }
  },
  {
    path: '/message',
    name: 'Message',
    component: Message,
    meta: {
      title: '留言板'
    }
  },
  {
    path: '/about',
    name: 'About',
    component: About,
    meta: {
      title: '我的故事'
    }
  },
  {
    path: '/links',
    name: 'Link',
    component: Link,
    meta: {
      title: '友情链接'
    }
  },
  {
    path: '/categories',
    name: 'Category',
    component: Category,
    meta: {
      title: '分类'
    }
  },
  {
    path: '/tags',
    name: 'Tag',
    component: Tag,
    meta: {
      title: '标签'
    }
  },
  {
    path: '/article/:articleId',
    name: 'article',
    component: Article
  },
  {
    path: '/user',
    component: User
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
